// Import Bootstrap functions first
@import "../node_modules/mdb-angular-ui-kit/assets/scss/bootstrap/functions";

// Define custom color variables
$text: #eee9e9;
$background: #023c83;
$primary: #eeee62;
$secondary: #d6d659;
$accent: #e9f416;
$warning: #ffc199;
$success: #145eb8;

$link-color: #eee9e9;
$active-link-color: $secondary;
$hover-link-color: $primary;

// Define custom theme colors map
$custom-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "accent": $accent,
  "light": $text,
  "danger": lighten($accent, 20%),
  "warning": lighten($warning, 20%),
  "success": $success,
  "info": $text,
);

// Import MDB variables and merge with custom colors
@import "../node_modules/mdb-angular-ui-kit/assets/scss/bootstrap/variables";

$theme-colors: map-merge($theme-colors, $custom-colors);

// Import MDB styles
@import "../node_modules/mdb-angular-ui-kit/assets/scss/mdb.scss";

body {
  background-color: $background;
  color: $text;
}

.navbar-nav .nav-link {
  color: $link-color;
}

.navbar-nav .nav-link.active {
  color: $active-link-color;
}

.navbar-nav .nav-link:hover {
  color: $hover-link-color;
}

